.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

img{
  	width: 175px
}

.product {
  background-color: lightblue;
  border: 1px solid black;
  border-radius: 25px;
  margin-left: 35%;
  margin-right: 35%;
  padding: 10px;
}

.grid-container {
  display: grid;
  grid-template-columns: auto auto;
}

.grid-container > div {
  text-align: center;
}

.cart {
  background-color: lightblue;
  border: 1px solid black;
  border-radius: 25px;
  margin-left: 30%;
  margin-right: 30%;
  text-align: center;
}

.form {
  text-align: center;
  margin-left: 25%;
  margin-right: 25%;
  padding: 10px;
}
